@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --Strong-cyan: hsl(172, 67%, 45%);
  --Very-dark-cyan: hsl(183, 100%, 15%);
  --Dark-cyan: hsl(186, 14%, 43%);
  --Dark-cyan-2: hsl(184, 14%, 56%);
  --Light-cyan: hsl(185, 41%, 84%);
  --Light-cyan-2: hsl(189, 41%, 97%);
  --White: hsl(0, 0%, 100%);
}
* {
  box-sizing: border-box;
  font-family: "Space Mono", sans-serif;
  margin: 0;
  padding: 0;
}
body {
  background-color: hsl(185, 41%, 84%);
  background-color: var(--Light-cyan);
}
.logo {
  height: 25vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.calculator-body {
  background-color: hsl(0, 0%, 100%);
  background-color: var(--White);
  height: 100vh;
  border-radius: 20px 20px 0 0;
  display: grid;
  grid-template-rows: 60% 40%;
}
.calc-input {
  display: grid;
  grid-template-rows: 25% 50% 25%;
}
.calc-output-body {
  background-color: hsl(183, 100%, 15%);
  background-color: var(--Very-dark-cyan);
  border-radius: 20px;
  height: 80%;
  margin: 10px 20px;
  display: grid;
  grid-template-rows: 25% 25% 50%;
}
.reset-button {
  display: flex;
  justify-content: center;
  align-items: center;
}
.reset-btn {
  border-radius: 5px;
  background-color: hsl(172, 67%, 45%);
  background-color: var(--Strong-cyan);
  width: 90%;
  height: 40%;
  border: 0;
  margin-top: 5%;
  font-size: 1em;
}

input {
  color: hsl(183, 100%, 15%);
  color: var(--Very-dark-cyan);
  font-size: 24px;
  font-weight: 600;
}
.input-title {
  font-size: 0.8em;
  color: hsl(183, 100%, 15%);
  color: var(--Very-dark-cyan);
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

input:focus {
  outline: none !important;
  border: 1px solid hsl(185, 41%, 84%);
  border: 1px solid var(--Light-cyan);
}

@media only screen and (min-width: 768px) {
  .calculator-body {
    width: 80%;
    margin: auto;
    height: 65vh;
    grid-template-columns: repeat(2, 50%);
    grid-template-rows: auto;
    border-radius: 20px;
  }
  .calc-input{
    height: 90%;
  }
  .calc-output {
    display: flex;
  }
  .calc-output-body {
    align-self: center;
    width: 100%;
  }
  .reset-button {
    align-items: flex-end;
  }
  .reset-btn {
    height: 25%;
    width: 80%;
    margin-bottom: 10%;
  }
  .result {
    margin: 10% 5% 0 10% !important;
  }
}
@media only screen and (min-width: 1024px) {
  .calculator-body{
    height: 70vh;
  }
  .calc-input{
    height: 65%;
  }
  .reset-btn {
    font-size: 1.5em;
  }
  .result-heading{
    font-size: 1.5em;
  }
  .tip-grid{
    height: 140%!important;
  }
  .calc-output{
    height: 75%;
  }
}

.result{
    margin: 5% 5% 0 5%;
    display: flex;
    justify-content: space-between;
}
.result-heading{
    color: var(--White);
}
.result-amount{
    color:var(--Strong-cyan);
    align-self: center;
    font-size: 2em;
    font-weight: 700;
}
.sm{
    font-size: 0.7em;
}
.input-div {
  margin: 20px 20px;
}
.input-field {
  border: 0;
  background-color: var(--Light-cyan-2);
  width: 100%;
  text-align: right;
  border-radius: 5px;
}

.input-sign {
  position: absolute;
  left: 20px;
  margin: 10px 10px;
  width: 12px;
}

.input-flex {
  display: flex;
  justify-content: space-between;
}
.bill-red-flag {
  color: rgba(255, 0, 0, 0.7);
  display: none;
}
.count-red-flag {
  color: rgba(255, 0, 0, 0.7);
  display: none;
}
.zero {
  display: block;
}
.zero-input:focus {
  border: 1px solid red !important;
}
@media only screen and (min-width: 768px) {
  .input-sign {
    left: 13%;
  }
}
@media only screen and (min-width: 1024px) {
  .input-sign {
    width: 20px;
    left: 14%;
  }
}

.tip {
  margin: 20px 20px;
}
.tip-grid {
  height: 90%;
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: repeat(3, 33%);
  grid-gap: 15px 10px;
  gap: 15px 10px;
  padding: 10px 0;
}
.tip-button {
  background-color: var(--Very-dark-cyan);
  border: 0;
  font-size: 24px;
  color: var(--White);
  align-self: center;
  font-weight: 700;
  border-radius: 5px;
}
.custom-input {
  height: 100%;
  text-align: right;
  width: 100%;
  border: 0;
  border-radius: 5px;
}
.custom-input::-webkit-input-placeholder {
  font-size: 18px;
  text-align: center;
  font-weight: 700;
}
.custom-input:-ms-input-placeholder {
  font-size: 18px;
  text-align: center;
  font-weight: 700;
}
.custom-input::placeholder {
  font-size: 18px;
  text-align: center;
  font-weight: 700;
}

.active {
  background-color: var(--Strong-cyan);
  color: var(--Very-dark-cyan);
}

@media only screen and (min-width: 768px) {
  .tip-grid {
    height: 80%;
    display: grid;
    grid-template-rows: 50% 50%;
    grid-template-columns: repeat(3, 33%);
    grid-gap: 0px 10px;
    gap: 0px 10px;
    padding: 10px 0;
  }
  .tip-button {
    height: 80%;
  }
  .custom-input-div{
    display: flex;
  }
  .custom-input {
    height: 80%;
    align-self: center;
  }
}

@media only screen and (min-width: 1024px) {
  .tip-button {
    height: 70%;
  }
  .custom-input {
    height: 70%;
  }
  .result-amount{
    font-size: 3em;
  }
  .input-div{
    padding: 20px;
  }
  .tip{
    padding: 20px;
  }
  .input-field {
    padding: 10px;
  }
  .input-title{
    font-size: 1.5em;
  }
}
